
import { defineComponent } from 'vue'
import { PAGE_SIZE } from "@/shared/constants/common.const";
import { ICM208002SC00_RES_LIST, ICM208002SC00_REQ_Body } from '@/shared/type/ICM208002SC00.type';
import pagination from '@/shared/components/Pagination/Pagination.vue';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

export default defineComponent({
    name: "icm208002sc00",

    components: {
        pagination,
        BreadCrumb,
        SelectOption
    },
    data() {
        const headers = [
            { columnName: "No." },
            { columnName: "View" },
            { columnName: "Terms and Condition Category Code" },
            { columnName: "Terms and Condition Category Name" },
            { columnName: 'Status' },
            { columnName: "Description" },
        ];
        return {
            headers,
            dataTable: [] as ICM208002SC00_RES_LIST[],
            totalCount: 0 as number,
            startingIndex: 1 as number,
            isResetPage: true as boolean,
            search: {
                searchTypeCode: "",
                searchValue: "",
                pageNumber: 1,
                pageSize: PAGE_SIZE,
                statusCode: ''
            } as ICM208002SC00_REQ_Body
        }
    },

    mounted() {
        this.requestTermAndCondtionCategoryList(false);
    },

    methods: {

        handlePagination(pageNumber: number) {
            this.search.pageNumber = pageNumber;
            this.requestTermAndCondtionCategoryList(false);
        },

        handleStartIndex(startIndex: number) {
            this.startingIndex = startIndex;
        },

        async requestTermAndCondtionCategoryList(isSearch: boolean) {
            const body: ICM208002SC00_REQ_Body = {
                searchTypeCode: this.search.searchTypeCode,
                searchValue: this.search.searchValue.trim(),
                statusCode: this.search.statusCode,
                pageNumber: isSearch ? this.search.pageNumber = 1 : this.search.pageNumber,
                pageSize: isSearch ? this.search.pageSize = 10 : this.search.pageSize
            };
            if (isSearch) {
                this.search.pageNumber = 1
                this.isResetPage = true
            }
            await this.$request.getRequest('ICM20800211', body, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.dataTable = res.body.list;
                        this.totalCount = res.body.totalCount;
                    }
                });
        },

        async handleClearSearch() {
            this.search.searchTypeCode = "";
            this.search.searchValue = "";
            this.search.pageNumber = 1;
            this.search.statusCode = ''
            this.search.pageSize = 10;
            this.requestTermAndCondtionCategoryList(false);
        },

        handleClickAction(actionType: string, rowData?: ICM208002SC00_RES_LIST) {
            switch (actionType) {
                case 'register':
                    this.$router.push({
                        path: `ICM208002SC01`,
                        query: { type: "register" }
                    });
                    break;
                case 'detail':
                    this.$router.push({
                        path: `ICM208002SC01`,
                        query: {
                            type: "edit",
                            categoryCode: rowData?.categoryCode
                        }
                    });
                    break;
                case 'search':
                    this.requestTermAndCondtionCategoryList(true);
                    break;
                case 'clearSearch':
                    this.handleClearSearch();
                    break;
            }
        },
    },
})
